.vacation-details-container {
  display: flex;
  justify-content: center;
  padding: 2rem;
}

.vacation-details-card {
  background-color: #87CEEB;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 2rem;
  width: 80%;
}

.vacation-title {
  text-align: center;
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: black; 
}

.vacation-location {
  text-align: center;
  font-size: 1.25rem;
  margin-bottom: 2rem;
  color: black; 
}

.details-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* Two columns */
  gap: 1rem; /* Space between boxes */
}

.details-box {
  background-color: #007BFF; /* Blue background */
  color: black;
  padding: 1rem;
  border-radius: 8px;
  text-align: center;
}

/* Modern Itinerary Styling */
.itinerary-container {
  margin-top: 2rem;
  background-color: #f0f8ff; /* Light blue */
  border-radius: 10px;
  padding: 1.5rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.itinerary-title {
  font-size: 1.8rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 1.5rem;
  color: #0047ab; /* Deep blue */
}

/* Attraction item layout */
.attraction-item {
  display: flex;
  flex-direction: column;
  align-items: center; /* Center content horizontally */
  text-align: center; /* Center text inside the items */
  margin-bottom: 20px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
}

/* Attraction info section (name, details, price) */
.attraction-info {
  margin-bottom: 10px; /* Space between info and photos */
}

.attraction-info p {
  margin-bottom: 8px;
  font-size: 16px;
  color: #34495e;
}

/* Attraction price styling */
.attraction-price {
  font-weight: bold;
  font-size: 18px;
  color: #e74c3c;
  margin-top: 10px; /* Add space above the price */
  margin-bottom: 10px; /* Add space below the price */
}

/* Styling for the attraction photos container */
.attraction-photos {
  display: flex;
  justify-content: center; /* Center photos horizontally */
  gap: 10px; /* Space between the photos */
  margin-top: 10px; /* Add some space above the photos */
}

.attraction-photo {
  width: 100px; /* Set a fixed width for each image */
  height: 100px; /* Set a fixed height for consistency */
  object-fit: cover; /* Ensure the image covers the space without distortion */
  border-radius: 8px; /* Add rounded corners for style */
  transition: transform 0.3s ease; /* Smooth hover effect */
}

.attraction-photo:hover {
  transform: scale(1.05); /* Slightly enlarge image on hover */
}

/* Day Title */
.day-title {
  text-align: center; /* Center the day title */
  font-size: 26px; /* Increase the font size */
  font-weight: bold; /* Make the title bold */
  color: #2c3e50; /* Dark color for the title */
  margin-bottom: 20px; /* Add space beneath the title */
  padding-top: 10px; /* Add padding at the top for better alignment */
  padding-bottom: 10px; /* Add space beneath the title */
  border-radius: 4px; /* Rounded corners */
  background-color: #87CEEB; /* Match the background of attractions */
  width: 100%; /* Full width */
  height: 100px; /* Fixed height to make it square */
  display: flex;
  align-items: center; /* Vertically center text */
  justify-content: center; /* Horizontally center text */
  border: 1px solid #ddd; /* Light border to define the box */
}

