/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Set the background image for the whole page */
body {
  background-image: url('https://images.unsplash.com/photo-1500835556837-99ac94a94552?w=600&auto=format&fit=crop&q=60&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxzZWFyY2h8M3x8dHJhdmVsfGVufDB8fDB8fHww'); /* Replace 'your-image-url.jpg' with the actual URL of the image */
  background-size: cover;  /* Ensures the image covers the entire viewport */
  background-position: center;  /* Centers the image */
  background-repeat: no-repeat;  /* Prevents the image from repeating */
  background-attachment: fixed;
  min-height: 100vh;  /* Ensures the background covers the entire viewport */
  font-family: Arial, sans-serif;
  color: white;  /* Make the text color white for better contrast */
}

/* App container styles */
.App {
  text-align: center;
  padding: 50px;
  backdrop-filter: blur(5px);  /* Optional: adds a slight blur to the background behind the content */
  background-color: rgba(0, 0, 0, 0.5);  /* Adds a semi-transparent black overlay for text visibility */
  border-radius: 8px;
  margin-top: 50px;
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;
}

/* Header style */
h1 {
  font-size: 3rem;
  margin-bottom: 20px;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);  /* Adds a shadow to make the title stand out */
}


