.vacation-form {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  max-width: 600px;
  margin: auto;
}

.vacation-form select,
.vacation-form input,
.vacation-form button {
  width: 100%;
  padding: 1rem;
  font-size: 1rem;
  border: 2px solid #ccc;
  border-radius: 8px;
}

.vacation-form button {
  grid-column: span 2;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}

.vacation-form button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.vacation-list-container {
  padding: 1rem;
  font-family: Arial, sans-serif;
}

.vacation-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3 equal columns */
  gap: 1rem; /* Adjust space between cards */
  max-width: 100%; /* Make sure it uses full available width */
  margin: 10px auto; /* Center the grid and add some space on top */
  justify-items: center; /* Center the cards horizontally within each grid item */
}


.vacation-card-container {
  display: flex;
  flex-wrap: wrap; /* Allows wrapping of cards to the next row */
  justify-content: space-between; /* Distributes the cards evenly with minimal space */
}

.vacation-card {
  width: calc(85% ); /* 3 cards per row, with minimal space */
  height: 150px; /* Ensure cards are square */
  background-color: orange; /* Set background to orange */
  color: white; /* Adjust text color for contrast */
  border: 1px solid #ddd;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.5rem;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

/* Optional: Ensures very little space between cards */
.vacation-card:not(:nth-child(3n)) {
  margin-right: 2px; /* Tiny space between cards */
}

.vacation-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
  background-color: darkorange; /* Slightly darker orange on hover */
}

.vacation-card h3 {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
}

.vacation-card p {
  margin: 0.2rem 0;
  font-size: 1rem;
  color: black;
}
